/* Toggle Switch */
.toggle-switch {
  position: relative;
  width: 50px;
  height: 25px;
}

.toggle-switch input {
  display: none;
}

.toggle-switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 25px;
  transition: 0.4s;
}

.toggle-switch .slider:before {
  position: absolute;
  content: "";
  height: 19px;
  width: 19px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  border-radius: 50%;
  transition: 0.4s;
}

.toggle-switch input:checked + .slider {
  background-color: #4caf50;
}

.toggle-switch input:checked + .slider:before {
  transform: translateX(25px);
}

/* Parks Table Container */
.parks-table-container {
  width: 1200px;
  height: 800px;
  background-color: white;
  overflow-y: scroll;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.parks-table {
  width: 100%;
  border-collapse: collapse;
}

.parks-table th,
.parks-table td {
  padding: 15px;
  text-align: left;
}

.parks-table th {
  background-color: #f4f4f4;
  font-weight: bold;
}

.parks-table td img {
  width: 100px;
  height: auto;
  border-radius: 5px;
}

.parks-table td p {
  font-size: 14px;
  color: #585858;
}

.park-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

/* Checklist Title */
.checklist-title {
  width: 800px;
  margin: 80px auto 40px auto;
  text-align: center;
  font-family: "Comic Sans MS", sans-serif;
  font-size: 48px;
  font-weight: bold;
  color: rgb(0, 0, 0);
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  animation: popIn 1s ease-out forwards;
}

@keyframes popIn {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

