/* Support Popup Container */
.support-me-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 900px;
  max-height: 80%;
  transform: translate(-50%, -50%);
  background: white;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  border-radius: 10px;
  overflow-y: auto; /* Enables scrolling if content exceeds height */
  padding: 20px;
  display: flex;
  flex-direction: column;
}

/* Close Button */
.close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  background: grey;
  border: none;
  font-size: 20px;
  cursor: pointer;
  width: 75px;
}

.close-button:hover {
  color: red;
  background-color: black;
}

/* Heading */
h1 {
  font-size: 32px;
  color: #4a90e2;
  font-weight: 600;
  margin-bottom: 20px;
  text-align: center;
  font-family: 'Pacifico', cursive;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

/* About Me Section (Image and Text) */
.about-me {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

/* Text Container */
.text-container {
  width: 50%;
  font-family: 'Dancing Script', cursive;
  color: #333;
  line-height: 1.6;
  font-size: 18px;
  background-color: #f4f4f4;
  padding: 15px;
  border-radius: 8px;
}

.text-container p {
  font-size: 20px;
  margin-bottom: 20px;
  padding-top: 10px;
  color: #555;
}

/* Image Container */
.image-container {
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 15px;
}

.van-photo {
  width: 100%;
  height: auto;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.van-photo:hover {
  transform: scale(1.1);
}

/* Donate & YouTube Buttons */
.support-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.donate-button,
.youtube-button {
  display: block;
  width: 100%;
  padding: 12px;
  margin: 10px 0;
  background-color: #4a90e2;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  max-width: 250px;
}

.donate-button:hover,
.youtube-button:hover {
  background-color: #357ab7;
}

/* Button Focus for Accessibility */
.donate-button:focus,
.youtube-button:focus {
  outline: none;
  box-shadow: 0 0 0 2px #4a90e2;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .support-me-popup {
    width: 80%;
    max-height: 90%; /* Adjust max height for better usability */
  }

  .about-me {
    flex-direction: column;
    gap: 20px;
  }

  .text-container,
  .image-container {
    width: 100%;
  }

  h1 {
    font-size: 28px;
  }

  .text-container {
    font-size: 16px;
    padding: 10px;
  }

  .text-container p {
    font-size: 18px;
  }

  .donate-button,
  .youtube-button {
    font-size: 14px;
    width: 200px;
  }
  .close-button {
    position: absolute;
    top: 10px;
    right: 15px;
    background: grey;
    font-size: .8rem;
    border: none;
    cursor: pointer;
    width: 45px;
  }


}

@media (max-width: 480px) {
  .support-me-popup {
    width: 90%;
    max-height: 90%;
    padding: 15px;
  }

  h1 {
    font-size: 24px;
  }

  .text-container {
    font-size: 14px;
  }

  .text-container p {
    font-size: 14px;
  }

  .donate-button,
  .youtube-button {
    font-size: 12px;
    width: 150px;
  }
}

@media (max-width: 320px) {
  h1 {
    font-size: 20px;
  }

  .text-container {
    font-size: 12px;
    padding: 10px;
  }

  .donate-button,
  .youtube-button {
    font-size: 12px;
    padding: 10px;
  }
}
