.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f0f0f0;
}

.navbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background: rgba(0, 0, 0, 0.815);
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  height: 60px;
  position: relative;
}

.title {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-size: 2.5rem;
  font-weight: bolder;
  text-align: center;
  font-family: "Verdana", "Comic Sans", cursive;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.nav-buttons {
  display: flex;
  align-items: center;
}

.home-button,
.support-button,
.auth-buttons button {
  background-color: rgba(100, 180, 255, 0.801);
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  font-size: 1.1rem;
  border: none;
  padding: 6px 18px;
  border-radius: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
}

.home-button {
  margin-right: 15px;
}

.home-button:hover,
.support-button:hover,
.auth-buttons button:hover {
  background-color: rgb(100, 180, 255);
  color: white;
}

.auth-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-buttons button {
  margin-left: 10px;
}

.auth-button-signout button {
  width: 150px;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 60px;
  width: 100%;
  max-width: 800px;
  margin-top: 80px;
}

.feature-box {
  padding: 30px;
  border: 1px solid #ccc;
  border-radius: 12px;
  background: white;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  text-decoration: none;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  overflow: hidden;
}

.feature-box:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
}

.feature-box img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 15px;
}

.feature-box h2 {
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  font-size: 1.5rem;
  color: #333;
  margin-top: 10px;
  transition: transform 0.3s ease;
}

.feature-box:hover h2 {
  animation: bounce 2.5s infinite;
  position: relative;
  color: rgb(0, 69, 197);
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.116);
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(7px);
  }
}

.decorative-design {
  margin-top: 10px;
  font-size: 1.2rem;
  color: #888;
  text-align: center;
}

/* Centering support box */
.support-box-container {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 40px;
}

.support-box {
  width: 350px; 
  text-align: center;
  cursor: pointer;
  padding: 30px;
  border: 1px solid #ccc;
  border-radius: 12px;
  background: white;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.support-box:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
}

.support-box img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 15px;
}

.support-box h2 {
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  font-size: 1.5rem;
  color: #333;
  margin-top: 10px;
  transition: transform 0.3s ease;
}

.support-box:hover h2 {
  animation: bounce 2.5s infinite;
  position: relative;
  color: rgb(0, 69, 197);
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.116);
}


@media (max-width: 768px) {
 
  .feature-box h2,
  .support-box h2 {
    font-size: 1.1rem; /* Reduce feature box title font size further */
  }

  .decorative-design {
    font-size: 0.9rem; /* Shrink decorative text */
  }

  /* Feature Grid Adjustments */
  .feature-grid {
    gap: 30px; /* Reduce grid gap */
    margin-top: 40px;
  }

  .feature-box {
    padding: 10px; /* Shrink padding */
    font-size: 0.9rem; /* Reduce overall text size */
  }

  .feature-box img {
    width: 90%; /* Shrink image width */
  }

  /* Support Box Adjustments */
  .support-box {
    width: 50%; /* Match the size of the feature boxes */
    padding: 20px; /* Adjust padding to match feature boxes */
  }
}

@media (max-width: 480px) {


  .feature-box h2,
  .support-box h2 {
    font-size: .8rem; /* Reduce feature box title font size further */
    
  }

  .decorative-design {
    font-size: 0.8rem; /* Shrink decorative text */
  }

  .feature-grid {
    gap: 20px; /* Reduce grid gap */
    margin-top: -10px;
  }

  .feature-box {
    padding: 8px; /* Smaller padding */
    height: auto;
   
  }

  .support-box {
    width: 50%; /* Match full width like feature boxes */
    padding: 8px; /* Adjust padding to match feature boxes */
    margin-bottom: 75px;
  }
}

