/* Spinner */
.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-top: 4px solid #000;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Home Container */
.home-container {
  background-image: url("../resources/van-life-zy.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 20px;
  text-align: center;
}

/* Packing Title */
#packing-title {
  width: 800px;
  margin: 40px auto 40px auto;
  text-align: center;
  font-family: "Comic Sans MS", sans-serif;
  font-size: 48px;
  font-weight: bold;
  color: rgb(0, 0, 0);
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  animation: popIn 1s ease-out forwards;
}

/* Packing List Container */
.packing-list-container {
  width: 1000px;
  height: 800px;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  overflow-y: auto;
  text-align: left;
}

.packing-item {
  font-size: 1.2em;
  margin-bottom: 10px;
  padding: 5px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-bottom: 1px solid #ddd;
  text-align: left;
  cursor: pointer;
}

.packing-item.packed {
  text-decoration: line-through;
  color: grey;
}

.packing-list-container ul {
  list-style-type: none;
  padding: 0;
}

.packing-list-container li {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
}

.packing-list-container li:nth-child(even) {
  background-color: #f1f1f1;
}

.packing-list-container li:hover {
  background-color: #e9e9e9;
}

/* Input Section */
.input-section {
  margin: 10px 0;
}

/* Activity Selection */
.activity-selection {
  margin: 20px 0;
}

.activity-options label {
  margin-right: 10px;
}

/* Spinner Container */
.spinner-container {
  margin-top: 100px;
  text-align: center;
  justify-content: center;
  display: flex;
}

/* Generate Button */
.generate-btn {
  padding: 10px 20px;
  font-size: 18px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.generate-btn:disabled {
  background-color: #cccccc;
}

/* Error */
.error {
  color: red;
  font-weight: bold;
  margin-top: 20px;
}

/* Packing List Input/Label/Button */
#packing-list input,
#packing-list label,
#packing-list button {
  display: inline-block;
  text-align: center;
  margin: 8px;
  font-family: "Comic Sans MS", sans-serif;
}

/* Activity Options */
.activity-options {
  display: flex;
  margin: 15px 215px;
  color: rgb(255, 255, 255);
  background-color: rgba(121, 121, 121, 0.747);
  border-radius: 15px;
}

.activity-options label {
  display: block;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}

/* Button Styles */
.home-button,
.login-button,
.signup-button {
  font-size: 18px;
  padding: 10px 20px;
  color: white;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.home-button:hover,
.login-button:hover,
.signup-button:hover {
  background-color: #45a049;
}

/* Title */
.title {
  font-size: 32px;
  font-weight: bold;
  color: white;
}

/* Generate Background */
#generate-background {
  background-color: rgba(255, 255, 255, 0.863);
  border-radius: 20px;
  padding: 10px;
  margin: 10px 0px 30px;
  font-family: "Comic Sans MS", sans-serif;
}


@media (max-width:480px){
  #packing-title {
    width: 300px;
    margin: 5px auto 40px auto;
    text-align: center;
    font-family: "Comic Sans MS", sans-serif;
    font-size: 2rem;
    font-weight: bold;
    color: rgb(0, 0, 0);
    padding: 20px;
    background: white;
    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    animation: popIn 1s ease-out forwards;
  }
  
  /* Packing List Container */
  .packing-list-container {
    width: 325px;
    height: 800px;
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    overflow-y: auto;
    text-align: left;
  }
  
  .packing-item {
    font-size: 1.2em;
    margin-bottom: 10px;
    padding: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-bottom: 1px solid #ddd;
    text-align: left;
    cursor: pointer;
  }
}

@media (max-width: 1080px) {
  #packing-title {
    width: 550px;
    margin: 20px auto;
    font-size: 3.5rem;
  }

  .packing-list-container {
    width: 700px;
    height: 800px;
  }

  .packing-item {
    font-size: 1.1rem;
  }

  .activity-options {
    flex-wrap: wrap;
    margin: 15px 50px;
  }

  .generate-btn {
    font-size: 1rem;
    padding: 8px 4px;
  }
}





@media (max-width: 768px) {
  #packing-title {
    width: 450px;
    margin: 20px auto;
    font-size: 3rem;
  }

  .packing-list-container {
    width: 500px;
    height: auto;
  }

  .packing-item {
    font-size: 1.1rem;
  }

  .activity-options {
    flex-wrap: wrap;
    margin: 15px 50px;
  }

  .generate-btn {
    font-size: 16px;
    padding: 8px 4px;
  }
}

/* Large Phones (max-width: 640px) */
@media (max-width: 640px) {
  #packing-title {
    width: 320px;
    margin: 10px auto;
    font-size: 2.4rem;
  }

  .packing-list-container {
    width: 350px;
    height: 400px;
  }

  .packing-item {
    font-size: 1rem;
  }

  .activity-options {
    flex-wrap: wrap;
    margin: 10px 30px;
  }

  .generate-btn {
    font-size: 1rem;
    padding: 6px 4px;
  }
}

/* Very Small Devices (max-width: 360px) */
@media (max-width: 360px) {
  #packing-title {
    width: 250px;
    margin: 5px auto;
    font-size: 1.6rem;
  }

  .packing-list-container {
    width: 300px;
    height: auto;
  }

  .packing-item {
    font-size: 0.9rem;
  }

  .activity-options {
    flex-wrap: wrap;
    margin: 5px 15px;
  }

  .generate-btn {
    font-size: 12px;
    padding: 4px 8px;
  }
}