.auth-error {
  color: #f44336;
  font-size: 1rem;
  margin-top: 10px;
  text-align: center;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  animation: slideInError 0.5s ease-out;
}

@keyframes slideInError {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

#auth-title {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: white;
  border-radius: 5px;
  font-size: 1.6rem;
  padding: 10px;
  width: 100%;
  max-width: 400px;
  margin: auto;
}

#auth-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

#auth-modal-content {
  background-image: url("../resources/User-login.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.25);
  width: 90%;
  max-width: 500px;
  height: auto;
  text-align: center;
  position: relative;
  animation: fadeIn 0.4s ease-out;
}

#auth-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.5rem;
  font-weight: bold;
  color: #888;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s;
}

#auth-close-button:hover {
  color: #000;
}

.auth-input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}

.auth-button {
  width: 100%;
  max-width: 350px;
  padding: 12px;
  margin: 15px auto;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
}

.auth-button:hover {
  background-color: #45a049;
  transform: scale(1.02);
}

#auth-google-button {
  background-color: #ffffffde;
  margin-top: 15px;
  color: hsl(0, 0%, 35%);
  padding: 10px;
  border-radius: 8px;
  display: flex;
  width: 250px;
  align-items: center;
  justify-content: center;
}

#auth-google-button img {
  width: 20px;
  height: 20px;
  margin-right: 8px;
}

#auth-google-button:hover {
  background-color: #c53628;
  color: white;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.user-input-container {
  position: relative;
  max-width: 400px;
  margin: auto;
}

.password-toggle-icon {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  font-size: 1.2rem;
  cursor: pointer;
  color: #888;
}

.password-toggle-icon:hover {
  color: #000;
}

.auth-links {
  margin-top: 15px;
  font-size: 0.9rem;
}

.auth-links a {
  color: #007bff;
  text-decoration: none;
  margin: 0 5px;
}

.auth-links a:hover {
  text-decoration: underline;
}

#auth-close-button-small {
  position: absolute;
  top: 5px;
  right: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: #888;
  background: none;
  border: none;
  cursor: pointer;
  transition: color 0.3s;
  width: 50px;
  background-color: white;
}

#auth-close-button-small:hover {
  color: #000;
}

#auth-p {
  margin-top: 10px;
  color: hsl(0, 0%, 20%);
  padding: 10px 16px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  border: 1px solid #ccc;
  max-width: 200px;
  width: auto;
  border-radius: 12px;
  font-size: 0.95rem;
  font-weight: 500;
  transition: background-color 0.3s, color 0.3s, transform 0.2s;
  margin-left: auto;
  margin-right: auto;
}

#auth-p-p {
  margin-top: 10px;
  color: hsl(0, 0%, 20%);
  padding: 8px 12px;
  background-color: #e6f7ff;
  border: 1px solid #b3e5fc;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
  transition: background-color 0.3s, color 0.3s;
  max-width: 200px;
  margin-left: auto;
  margin-right: auto;
}

#auth-p:hover {
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border-color: #0056b3;
  transform: scale(1.05);
}

#auth-p:active,
#auth-p-p:active {
  background-color: #0056b3;
  color: #e0e0e0;
  transform: scale(0.98);
}




@media (max-width: 480px){
  #auth-modal-content{
    width: 70%;
    margin: auto;

  }

  #auth-title{
    margin-top: 20px;
    font-size: 1rem;
  }

  #auth-close-button-small{
    width: 35px;
    height: 35px;
  }
}